#Shop-Grid{
    width: 100%;
    height: 100%;
    color: var(--color-black-light);
    -webkit-animation: FadeIn 1.5s;
            animation: FadeIn 1.5s;
}
#Shop-Grid p.Title{
    text-align: left;
    text-transform: uppercase;
    font-weight: 600;
    height: 1.5rem;
}
#Shop-Grid-Featured{
    height: 8.5rem;
    padding: 0.75rem 0;
}
#Shop-Grid-Featured>div.Content{
    height: calc(100% - 1.5rem);
}
#Shop-Grid-Featured>div.Content div.slick-center div.Card-Featured{
    -webkit-transform: scale(1);
            transform: scale(1);
}
#Shop-Grid-Brands{
    height: calc(100% - 8.5rem);
    padding-top: 1rem;
}
#Shop-Grid-Brands>div.Pagination{
    height: 2.5rem;
    display: flex;
    align-items: center;
    justify-content: center;
    position: fixed;
    bottom: 7.5rem;
    left: 50%;
    -webkit-transform: translateX(-50%);
            transform: translateX(-50%);
    -webkit-transition: 1s;
    transition: 1s;
}
#Shop-Grid-Brands>div.Pagination>p{
    color: var(--color-grey-normal);
    font-size: var(--font-size-large);
}
#Shop-Grid-Brands>div.Pagination>p:hover{
    cursor: not-allowed;
}
#Shop-Grid-Brands>div.Pagination>p.Active{
    color: var(--color-gold-dark);
}
#Shop-Grid-Brands>div.Pagination>p.Active:hover{
    cursor: pointer;
}
#Shop-Grid-Brands>div.Pagination>span{
    margin: 0 1.25rem;
    padding: 0.25rem 1rem;
    color: var(--color-black-dark);
    text-align: center;
    font-weight: 600;
    border-bottom: 1px solid var(--color-black-dark);
    -webkit-animation: PageBorderShining 0.75s ease-in-out infinite alternate;
            animation: PageBorderShining 0.75s ease-in-out infinite alternate;
}
#Shop-Grid-Brands>div.Pagination>span:hover{
    cursor: text;
}
@-webkit-keyframes PageBorderShining {
    0%{border-color: var(--color-black-dark)}
    100%{border-color: transparent}
}
@keyframes PageBorderShining {
    0%{border-color: var(--color-black-dark)}
    100%{border-color: transparent}
}
#Shop-Grid-Brands>div.Content{
    height: calc(100% - 4rem);
    display: flex;
    flex-wrap: wrap;
    align-items: baseline;
    justify-content: space-between;
}

#Shop-Grid-Brands>div.Content>div.Card{
    width: 20%;
}

#Shop-Jump-Content{
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: center;
}
#Shop-Jump-Content>p{
    color: var(--color-black-light);
    font-size: var(--font-size-large);
    font-weight: 600;
    margin-bottom: 1.5rem;
}
#Shop-Jump-Content>input{
    text-align: center;
    margin-bottom: 1.5rem;
    width: 10rem;
}
#Shop-Jump-Content>button{
    padding: 0.25rem 1.5rem;
    min-width: 10rem;
}
/* Suppose all cards' initial size is 480*300 */
div.Card{
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: flex-start;
}
div.Card>p.CardName{
    font-size: var(--font-size-small);
    color: var(--color-black-dark);
}
div.Card>p.CardCountry{
    font-size: var(--font-size-tiny);
}
div.Card:hover{
    cursor: pointer;
}
div.Card-Featured{
    -webkit-transform: scale(0.8);
            transform: scale(0.8);
    -webkit-transition: all 0.75s;
    transition: all 0.75s;
}
div.Card-Featured:hover{
    -webkit-transform: scale(1);
            transform: scale(1);
}
div.Card-Featured>img{
    width: 6rem;
}
div.Card-Normal{
    margin: 0.5rem 0.25rem;
}
div.Card-Normal>img{
    width: 100%;
}
div.Card-Normal:hover>img{
    -webkit-animation: HorinzontalShaking ease-in-out 0.5s;
            animation: HorinzontalShaking ease-in-out 0.5s;
}
div.Card-Denomination,
div.Card-Arbitrary{
    margin-left: auto;
    margin-right: auto;
}
div.Card-Denomination>img,
div.Card-Arbitrary>img{
    width: 100%;
}
div.Card-Denomination>p.CardValue,
div.Card-Arbitrary>p.CardValue{
    color: var(--color-black-dark);
    font-weight: 700;
    margin-top: 0.5rem;
}

